@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@300;400;500;600;700&family=Outfit:wght@300;400;500;600;700;800&display=swap');

:root {
  --bg-0: #0A0B0E;
  --bg-1: #0F1115;
  --bg-2: #161920;
  --bg-3: #1C2029;
  --bg-4: #232833;
  --border: #2A2F3C;
  --border-light: #363D4E;
  --text-0: #F4F5F7;
  --text-1: #C8CCD6;
  --text-2: #8B919F;
  --text-3: #5C6270;
  --accent: #E8A838;
  --green: #4AE68A;
  --red: #FF4B4B;
  --blue: #5BCEFC;
  --purple: #7B8CFF;
  --pink: #FF6B9D;
  --orange: #E8A838;
  --glow-green: 0 0 20px rgba(74,230,138,0.15);
  --glow-red: 0 0 20px rgba(255,75,75,0.15);
  --glow-blue: 0 0 20px rgba(91,206,252,0.15);
}

* { margin: 0; padding: 0; box-sizing: border-box; }

body {
  background: var(--bg-0);
  color: var(--text-0);
  font-family: 'Outfit', sans-serif;
  overflow: hidden;
}

::-webkit-scrollbar { width: 4px; }
::-webkit-scrollbar-track { background: transparent; }
::-webkit-scrollbar-thumb { background: var(--border-light); border-radius: 2px; }
::-webkit-scrollbar-thumb:hover { background: var(--text-3); }

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.4; }
}
@keyframes slideIn {
  from { opacity: 0; transform: translateY(8px); }
  to { opacity: 1; transform: translateY(0); }
}
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}
@keyframes treeLineGrow {
  from { height: 0; }
  to { height: 100%; }
}
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

/* Reset for form elements */
input, textarea, select, button {
  font-family: inherit;
  outline: none;
}

textarea:focus, input:focus, select:focus {
  border-color: var(--accent) !important;
}
